import { Component, OnInit } from '@angular/core';
import { CimaService } from '../../service/cima.service';
import { User } from '../../models/user.model';
import * as moment from 'moment';


declare var $: any;
declare var M: any;
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  public inicio: User;

  constructor(private _cimaService: CimaService) {
    this.inicio = new User('', '', '', 0, '', '', null, 'hombre', '', '', '', null, '', null);
  }

  ngOnInit() {
    $(document).ready( function(){
      $('select').formSelect();
      M.updateTextFields();
      $('.datepicker').datepicker({ 
        firstDay: true, 
        format: 'dd-mm-yyyy',
        i18n: {
            months: ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"],
            monthsShort: ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul", "Ago", "Set", "Oct", "Nov", "Dic"],
            weekdays: ["Domingo","Lunes", "Martes", "Miércoles", "Jueves", "Viernes", "Sábado"],
            weekdaysShort: ["Dom","Lun", "Mar", "Mie", "Jue", "Vie", "Sab"],
            weekdaysAbbrev: ["D","L", "M", "M", "J", "V", "S"]
        }
    });
    })
    console.log(this.inicio);
  }

  registrarUsuario(usuario) {
   
     usuario.access = 1;
     usuario.fechaRegistro = moment();
     
      this._cimaService.registrarUsuario(usuario).subscribe((usuario: any) => {
        if (usuario) {
          $("#nombre,#apellido,#celular,#password,#nacimiento,#direccion,#provincia,#localidad,#img,#mail").val("");
         alert("Listo");
        }
      }, err => {
        console.log(err);
      })
    
  }

}
