import { Component, OnInit } from '@angular/core';
import { Producto } from 'src/app/models/producto.model';
import { CimaService } from '../../service/cima.service';

declare var $: any;
declare var M: any;

@Component({
  selector: 'app-productregister',
  templateUrl: './productregister.component.html',
  styleUrls: ['./productregister.component.css']
})
export class ProductregisterComponent implements OnInit {

  public inicioProduc: Producto;

  constructor(private _cimaService: CimaService) { 
    this.inicioProduc = new Producto('', [''], '', '', '', '', '', '', '', '', null, null, '', 'hombre');
  }

  ngOnInit() {

    console.log(this.inicioProduc);
    $(document).ready(function(){
      $('select').formSelect();
      $('input#input_text, textarea#textarea2').characterCounter();
    });
    
  }

  registrarUsuario(producto){
    console.log(producto);

    this._cimaService.registrarProducto(producto).subscribe((usuario: any) => {
      if (usuario) {
        // $("#nombre,#apellido,#celular,#password,#nacimiento,#direccion,#provincia,#localidad,#img,#mail").val("");
       alert("Listo");
      }
    }, err => {
      console.log(err);
    })
  }

}
