  registrarUsuario(usuario: User){
    let params = JSON.stringify(usuario);
    let headers = new HttpHeaders().set('Content-Type', 'application/json');

    return this.http.post(this.url+"/register/", params, {headers: headers}).pipe(map(
      resp => { return resp;}
    ));
  }

