import { ModuleWithProviders } from '@angular/core';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { InicioComponent } from './components/inicio/inicio.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { ProductslistComponent } from './components/productslist/productslist.component';
import { IteminfoComponent } from './components/iteminfo/iteminfo.component';
import { CarritoComponent } from './components/carrito/carrito.component';
import { MyaccComponent } from './components/myacc/myacc.component';
import { AdminpanelComponent } from './components/adminpanel/adminpanel.component';
import { UserinfoComponent } from './components/userinfo/userinfo.component';
import { ProductinfoComponent } from './components/productinfo/productinfo.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { ProductregisterComponent } from './components/productregister/productregister.component';


const routes: Routes = [
    {path: '', component: InicioComponent},
    {path: 'registrar-producto', component: ProductregisterComponent},
    {path: 'productos', component: ProductslistComponent},
    {path: 'producto', component: ProductinfoComponent},
    {path: '**', component: InicioComponent}
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {}
export const routingComponents = [
    InicioComponent,
    ProductregisterComponent,
    ProductslistComponent,
    IteminfoComponent,
    CarritoComponent,
    MyaccComponent,
    AdminpanelComponent,
    UserinfoComponent,
    ProductinfoComponent,
    LoginComponent,
    RegisterComponent,
    NavbarComponent
]